import {
  Input,
  Component,
  NgModule,
  ModuleWithProviders,
  ViewEncapsulation
} from '@angular/core';

import {PlatformModule} from '../../platform/index';
import {CommonModule} from '@angular/common';

@Component({
  moduleId: module.id,
  selector: 'vdl-hint',
  host: {
    'class': 'vdl-hint',
    '[class.vdl-error]': '_isError()',
  },
  styleUrls: ['hint.css'],
  templateUrl: 'hint.html',
  encapsulation: ViewEncapsulation.None,
})
export class VdlHint {
  @Input() error: string;

  @Input() hintText: string;

  _isError() {
    if (this.error == '') {
      return true;
    }
  }
}

@NgModule({
  declarations: [
    VdlHint
  ],
  imports: [
    CommonModule,
    PlatformModule
  ],
  exports: [
    VdlHint
  ],
})
export class VdlHintModule {
  static forRoot(): ModuleWithProviders {
    return {
      ngModule: VdlHintModule,
      providers: PlatformModule.forRoot().providers,
    };
  }
}
